<template>
  <ais-instant-search :search-client="searchClient" index-name="PRODUCTS_REL" :routing="routing" style="align-items:center;">
    <div style="display: flex; align-items:center; flow-wrap: wrap; direction: row;">
      <div style="flex-basis: 80%; direction: column;">
        <div> 
          <p style="padding-bottom: 3%; color:#23263b">貨幣: &nbsp;<a href="#" @click="convertPrice('ntd')" style="text-decoration: none;">
          <img src="https://upload.wikimedia.org/wikipedia/commons/e/ef/Taiwanese_flag_emoji.svg"
              style="max-width:5%; height:auto;"/>TWD</a> &nbsp;
          <a href="#" @click="convertPrice('won')" style="text-decoration: none;">
          <img src="https://upload.wikimedia.org/wikipedia/commons/0/09/Flag_of_South_Korea.svg"
              style="max-width:5%; height:auto;"/>KRW</a></p>
        </div>

        <div>
          <ais-search-box style="max-width:100%; height:auto;" placeholder="品牌、品項、品名(如：BYOUR、洗髮、VT+面膜)" />
          <h1 style="float:right;"><a href="https://shoongdev.github.io/pop" style="color:#f24443" target='_blank'>人氣商品TOP 100</a></h1>
        </div>
      </div>
      <div
        style="flex-basis: 20%; display: flex; justify-content: center; align-items:center; flow-wrap: wrap; flex-direction: column; padding-left: 3%;">
        <img src="@/assets/banner.jpg" style="max-width:100%; height:auto;">
        <a href="https://lin.ee/b1eVSK1" target='_blank'><img src="@/assets/line_friend.png"
            style="max-width:100%; height:auto;"></a>
      </div>
    </div>
    <h1 style="padding-bottom: 3%; color:#DB7093">產品圖案或許會與實際品項有異，訂購時，敬請咨詢確認。感謝您。</h1>
    <ais-dynamic-widgets>
      <ais-refinement-list attribute="inven_info.sl"/>
    </ais-dynamic-widgets>
    <div class="row-display" style="align-items: flex-start; width:100%;">
      <div class="column-display" style="flex-basis: 100%; margin-left:2%; justify-content: flex-start; align-items: flex-start;">
        <ais-hits>
          <template v-slot:item="{ item }">
              <div class="row-display" style="width:100%;">
                <div class="column-display" style="flex-basis: 25%; width:100%">
                  <div>
                    <img v-bind:src="item.prod_image" style="width:100%; height:auto;"/>
                  </div>
                  <p>
                    <ais-highlight :hit="item" attribute="prod_code" />
                  </p>
                </div>
                <div class="column-display" style="flex-basis: 75%; margin-left: 1%; justify-content: flex-start; align-items: flex-start;">
                  <div class="column-display" style="justify-content: flex-start; align-items: flex-start; flex-wrap: wrap;">
                    <h2 style="color:#FF69B4" v-if="item.inven_info">
                      <span style="color:#4D4DFF" v-if="(item.inven_info.tw && item.inven_info['3pl'] &&
                      item.inven_info.tw.inven > 0 && item.inven_info['3pl'].inven > 0)">[箱拿/散拿現貨商品]</span>
                      <span style="color:#34925E" v-else-if="item.inven_info['3pl'] && item.inven_info['3pl'].inven > 0">[箱拿現貨商品]</span>
                      <span style="color:#34925E" v-else-if="item.inven_info.tw && item.inven_info.tw.inven > 0">[散拿現貨商品]</span>
                      <span style="color:#1E90FF" v-else-if="item.inven_info.kr && item.inven_info.kr.inven > 0">[現貨商品]</span>
                      <ais-highlight :hit="item" attribute="prod_name_tw" />
                    </h2>
                    <h2 style="color:#1E90FF" v-else>
                      <ais-highlight :hit="item" attribute="prod_name_tw" />
                    </h2>
                    <p v-show="currentPriceUnit === 'won'">
                      <ais-highlight :hit="item" attribute="prod_name_kr" />
                    </p>
                  </div>
                  <div class="row-display" style="margin-top:2%; margin-bottom:2%; width:100%">
                    <table>
                      <thead>
                        <tr>
                          <th v-show="item.weight !== '0' &&  item.weight > 0 "><p>重量(g)</p></th>
                          <th v-show="item.min_pur_unit"><p>箱入數</p></th>
                          <th v-if="checkInventory(item.inven_info)"><p>現貨數量</p></th>
                          <th><p>供給價格</p></th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td v-show="item.weight !== '0' &&  item.weight > 0 ">
                            <h2>{{ item.weight }}</h2>
                          </td>
                          <td v-show="item.min_pur_unit">
                            <h2>{{ item.min_pur_unit }}</h2>
                          </td>
                          <td v-if="checkInventory(item.inven_info)">
                            <h2 style="color:#1E90FF">{{ getInvenTotal(item.inven_info) }}</h2>
                          </td>
                          <td >
                            {{ getNTDpriceByWon(item) }}
                            <h2 :style="backColorByCurrency()">{{ currentPriceMap[item.prod_code].displayPrice }}</h2>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
          </template>
        </ais-hits>
        </div>
      </div>
      <ais-pagination :padding="2"/> 
  </ais-instant-search>
</template>

<script>
import algoliasearch from 'algoliasearch/lite';
import 'instantsearch.css/themes/satellite-min.css';
import { history as historyRouter } from 'instantsearch.js/es/lib/routers';
import { singleIndex as singleIndexMapping } from 'instantsearch.js/es/lib/stateMappings';
import { authComputed } from './vuex/helpers.js'

export default {  
  computed: {
    ...authComputed
  },
  props: {
    item: Object
  },
  data() {
    return {
      searchInitFlag: false,
      searchClient: algoliasearch(
        'SZOTLIDLJD',
        '5283f978a80eac551ed19eac8cfb6f3d'
      ),
      routing: {
        router: historyRouter(),
        stateMapping: singleIndexMapping('PRODUCTS_REL'),
      },
      currentPriceUnit: 'ntd',
      currentPriceMap:{},
    }
  },
  created() {
  },
  methods: {
    checkInventory(invenInfo){
      if(invenInfo){
        if(invenInfo.kr && invenInfo['kr']['inven'] > 0){
          return true
        } else if(invenInfo.tw && invenInfo['tw']['inven'] > 0){
          return true
        } else if(invenInfo['3pl'] && invenInfo['3pl']['inven'] > 0){
          return true
        } else {
          return false
        }
      }
    },
    backColorByCurrency(){
      if(this.currentPriceUnit === 'ntd') {
        return {'color': '#1E90FF' }
      } else {
        return {'color': '#444444' } 
      }
    },
    getInvenInfo(invenInfo) {
      let totalInven = 0
      function getInven(region) {
        if(invenInfo[region]) {
          return invenInfo[region]['inven']
        }
        return 0
      }
      
      totalInven += getInven('tw')
      totalInven += getInven('kr')
      totalInven += getInven('3pl')

      return totalInven
    },
    getInvenTotal(invenInfo) {
      let totalInven = 0
      function getInven(region) {
        if(invenInfo[region]) {
          return invenInfo[region]['inven']
        }
        return 0
      }
      
      totalInven += getInven('tw')
      totalInven += getInven('kr')
      totalInven += getInven('3pl')

      return totalInven
    },
    searchFunction(helper) {
      if(this.searchInitFlag == true && helper.state.query.length > 1) {
        helper.search()
      }else {
        if(this.searchInitFlag == false) {
          this.searchInitFlag = true
          helper.search()
        }
      }
    },
    convertPrice(priceUnit) {
      this.currentPriceUnit = priceUnit
    },
    getNTDpriceByWon(item) {
      let prodCd = item.prod_code
      let currentProdCode = this.currentPriceMap[prodCd]
      if(!currentProdCode)
        this.currentPriceMap[prodCd] = { stock_price: item.stock_price, weight: item.weight,
          stock_price_c_v: item.stock_price_c_v , ex_rate: item.ex_rate, margin_transp_f: item.margin_transp_f,
          margin_transp_val: item.margin_transp_val, margin_fee: item.margin_fee, count: item.count, prod_code: prodCd,
          prod_image: item.prod_image, prod_name_kr: item.prod_name_kr, prod_name_tw: item.prod_name_tw}

      if(item.moq){
        this.currentPriceMap[prodCd].moq = item.moq
      }

      if(item.min_pur_unit) {
        this.currentPriceMap[prodCd].min_pur_unit = item.min_pur_unit
      }

      this.convertPriceInPriceUnitMap(prodCd)
    },
    convertPriceInPriceUnitMap(prodCd) {
      let { weight }= this.currentPriceMap[prodCd]

      if(weight === '' || weight === undefined || !weight || weight === '0' ||  weight == 0){
        this.currentPriceMap[prodCd].sales_price = this.calSalesPriceBy10(this.currentPriceMap[prodCd])
        this.currentPriceMap[prodCd].price_unit = 'won'
        this.currentPriceMap[prodCd].displayPrice = this.currentPriceMap[prodCd].sales_price.toLocaleString() + " won"
      } else {
        if(this.currentPriceUnit === 'ntd') {
          this.currentPriceMap[prodCd].sales_price = this.calSalesPriceMulWithWeight(this.currentPriceMap[prodCd])
          this.currentPriceMap[prodCd].sales_price_tw = this.calSalesPriceMulWithWeightAndExRate(this.currentPriceMap[prodCd])
          this.currentPriceMap[prodCd].price_unit = 'ntd'
          this.currentPriceMap[prodCd].displayPrice = this.currentPriceMap[prodCd].sales_price_tw.toLocaleString() + " NTD"
        } else {
          this.currentPriceMap[prodCd].sales_price = this.calSalesPriceBy10(this.currentPriceMap[prodCd])
          this.currentPriceMap[prodCd].price_unit = 'won'
          this.currentPriceMap[prodCd].displayPrice = this.currentPriceMap[prodCd].sales_price.toLocaleString() + " won"
        }
      }
    },
    transformItems(items) {
    this.searchInitFlag = false
    return items
  },
  },
  watch: {
    currentPriceUnit() {
      /*
      todo!------------------------------------------------------clear currentPriceMap whenever ais-hit is validated
      */
      let keys = Object.keys(this.currentPriceMap)
      for(var key in keys){
        this.convertPriceInPriceUnitMap(keys[key])
      }
    }
  },
  
}
</script>

<style>
body {
  font-family: sans-serif;
}

.row-display {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.column-display {
  display: flex; 
  justify-content: center; 
  align-items:center; 
  flex-wrap: wrap; 
  flex-direction: column;
}

.item[aria-selected=true] {
  color: #1E90FF;
  font-weight: bold;
}

.flex-row-for-item {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.ais-RefinementList-list{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.ais-Hits-item {
  padding: 0.5rem;
}

:root {
  --font-size-lg: clamp(0.7rem, 2vw, 1.7rem);
  --font-size-md: clamp(0.6rem, 2vw, 1.6rem);
  --font-size-sm: clamp(0.5rem, 2vw, 1.5rem);
}

.ais-RangeInput-input,
.ais-SearchBox-input {
  font-size: var(--font-size-md);
}

br {
  font-size: var(--font-size-sm);
}

h1 {
  font-size: var(--font-size-lg);
  margin-bottom: 1%;
}

h2 {
  font-size: var(--font-size-md);
  margin-top: 0;
  margin-bottom: 1%;
}

p {
  font-size: var(--font-size-sm);
  margin-top: 0;
  margin-bottom: 1%;
}

blockquote {
  font-size: var(--font-size-md);
}

a {
  font-size: var(--font-size-sm);
}

.basicList_etc_box__5lkgg .basicList_etc__LSkN_~.basicList_etc__LSkN_:before {
    display: inline-block;
    width: 2px;
    height: 2px;
    margin: 6px 7px 0;
    background-color: #bababa;
    vertical-align: top;
    content: "";
}

table {
    width: 100%;
    border: 1px solid #cccccc;
    border-collapse: collapse;
    text-align: center;
  }
th, td {
    border: 1px solid #cccccc;
}

</style>
