import { createStore } from 'vuex'

function stockPriceDividedBy10(priceInfo){
  let stockPrice = priceInfo.stock_price
  if(priceInfo.stock_price_c_v === 1) {
    stockPrice = stockPrice / priceInfo.margin_transp_f
  }
  return stockPrice
}

const store = createStore({
  state: {
    currentPriceUnit: 'ntd',
  },
  mutations: {
  },
  actions: {
  },
  getters: {
    calSalesPriceWithExRate() {
      return (priceInfo) => {
        return (Math.round((stockPriceDividedBy10(priceInfo) * priceInfo.ex_rate) * priceInfo.margin_transp_f))
      }
    },
    calSalesPrice() {
      return (priceInfo) => {
        return (Math.round(stockPriceDividedBy10(priceInfo) * priceInfo.margin_transp_f))
      }
    },
    calSalesPriceBy10() {
      return (priceInfo) => {
        return (Math.round((stockPriceDividedBy10(priceInfo) * priceInfo.margin_transp_f) / 10) * 10)
      }

    },
    calSalesPriceMulWithWeight() {
      return (priceInfo) => {
        return (Math.round( (((stockPriceDividedBy10(priceInfo) + (priceInfo.margin_transp_val * priceInfo.weight)) * priceInfo.margin_fee ) * priceInfo.margin_transp_f)))
      }

    },
    calSalesPriceMulWithWeightAndExRate() {
      return (priceInfo) => {
        return (Math.round( (((stockPriceDividedBy10(priceInfo) + (priceInfo.margin_transp_val * priceInfo.weight)) * priceInfo.margin_fee / priceInfo.ex_rate ) * priceInfo.margin_transp_f)))
      }
    }
  }
})

export default store;
